import * as okayapi from '../../../components/okayapi/okayapi.js';


export default {
	data() {
		return {
			title: 'login',
			first: '',
			nickname: '',
			password: '',
		}
	},
	methods: {
		login: function() {
			var params = {
				s: "App.Table.FreeQuery", // 必须，待请求的接口服务名称
				model_name: 'user',
				where: '[["username", "=", "' + this.nickname + '"],["user_password", "=", "' + this.password + '"]]'
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					console.log(res);
					let data = res.data.data.list
					if (data.length == 1) {
						uni.showToast({
							icon: 'success',
							title: '登入成功',
						})
						this.user = data[0]
					} else {
						uni.showToast({
							icon: 'none',
							title: '登入失败,请确认账号密码',
						})
					}
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
			var str = '';
			for (let key in okayapi.enryptData(params)) {
				str += key + "=" + okayapi.enryptData(params)[key] + '&';
			}
			this.first = this.okayapiHost + '?' + str;
		},
	}
}
